import React, { useEffect } from "react";
import { Link, graphql } from "gatsby";
import rehypeReact from "rehype-react";
import SEO from "../../components/SEO";
import Layout from "../../components/layout";
import ReactGA from 'react-ga';

import "./styles.scss";

const renderAst = new rehypeReact({
  createElement: React.createElement,
}).Compiler

export default function Template({
  data
}) {
  useEffect(() => {
    ReactGA.initialize("UA-88285026-2");
    ReactGA.pageview(window.location.pathname + window.location.search);
  })

  const { markdownRemark,} = data;
  const { excerpt, frontmatter, htmlAst, id } = markdownRemark;
  return (
    <Layout>
      <SEO
        title={frontmatter.title}
        img={frontmatter.cover_photo}
        description={excerpt}
        keywords={frontmatter.tags}
        urlPath={`${id}`}
      />
      <section className="blog-post-container">
        <div className="blog-post">
          <h1 className="Title">{frontmatter.title}</h1>
          <div className="Date">{frontmatter.date}</div>
          <img src={frontmatter.cover_photo} alt={frontmatter.title} />

          <div className="blog-post-content">
            {renderAst(htmlAst)}
          </div>

          <div className="blog-post-tags">
            {frontmatter.tags.map((tag) => {
              return (<Link key={tag} className="blogTag" to={`/${tag.toLowerCase()}`}>#{tag}</Link>);
            })}
          </div>
        </div>
      </section>
    </Layout>
  )
}

export const pageQuery = graphql`
  query($slug: String!) {
    markdownRemark(frontmatter: { slug: { eq: $slug } }) {
      htmlAst
      id
      excerpt
      frontmatter {
        date(formatString: "MMMM D, YYYY")
        slug
        title
        tags
        cover_photo
      }
    }
  }
`